'use client';

import React from 'react';
import { useRouter } from 'next/navigation';
import { signOutUser } from '@/src/components/auth/authService';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFileContract } from '@fortawesome/free-solid-svg-icons';
import {
  FaTimes, FaPencilAlt, FaUser, FaCalendarAlt, FaEnvelope,
  FaBuilding, FaCog, FaStore, FaMotorcycle, FaUserCog, FaBatteryFull,
} from 'react-icons/fa';
import { useUser } from '@/src/context/AuthContext';
import { IconType } from 'react-icons';

interface UserSidebarProps {
  user: any;
  isOpen: boolean;
  onClose: () => void;
}

type MenuItem =
  | { divider: true }
  | {
      icon: IconType | typeof faFileContract;
      label: string;
      path?: string;
      onClick?: () => void;
    };

const UserSidebar: React.FC<UserSidebarProps> = ({ user, isOpen, onClose }) => {
  const router = useRouter();
  const { role } = useUser();

  if (!isOpen) return null;

  const normalizedRole = (role || '').toLowerCase();

  const commonItems: MenuItem[] = [
    { icon: FaPencilAlt, label: 'Write a review', onClick: () => {} },
    { divider: true },
    { icon: FaUser, label: 'Profile', path: '/profile' },
    { icon: FaCalendarAlt, label: 'Bookings', path: '/bookings' },
    { icon: FaEnvelope, label: 'Messages', path: '/messages' },
    { icon: FaBuilding, label: 'My business', path: '/my-business' },
    { divider: true },
  ];

  const supportItems: MenuItem[] = [
    { icon: FaMotorcycle, label: 'Rent a Bike', path: '/rent' },
    { icon: FaMotorcycle, label: 'Return a Bike', path: '/return' },
  ];

  const stationManagerItems: MenuItem[] = [
    ...supportItems,
    { icon: FaStore, label: 'Station Overview', path: '/stats' },
  ];

  const technicianItems: MenuItem[] = [
    { icon: FaMotorcycle, label: 'Maintenance Logs', path: '/maintain' },
  ];

  const fullAdminItems: MenuItem[] = [
    { icon: FaStore, label: 'Rental Companies', path: '/rental-companies' },
    { icon: FaMotorcycle, label: 'Ebike Management', path: '/ebikeManagement' },
    { icon: FaUser, label: 'Users Management', path: '/users' },
    { icon: FaUserCog, label: 'Customers Management', path: '/customers' },
    { icon: FaBatteryFull, label: 'Battery Management', path: '/battery' },
    { icon: faFileContract, label: 'Subscription Packages', path: '/subscriptionPackages' },
    { icon: FaPencilAlt, label: 'Form Builder', path: '/my-business/form-builder' },
  ];

  const formBuilderItem: MenuItem = {
    icon: FaPencilAlt,
    label: 'Form Builder',
    path: '/my-business/form-builder',
  };

  const footerItem: MenuItem = {
    icon: FaCog,
    label: 'Account Info',
    path: '/account',
  };

  const getMenuItems = (): MenuItem[] => {
    if (normalizedRole === 'support') {
      return [...commonItems, ...supportItems, { divider: true }, footerItem];
    }

    if (normalizedRole === 'station_manager') {
      return [...commonItems, ...stationManagerItems, { divider: true }, footerItem];
    }

    if (normalizedRole === 'technician') {
      return [...commonItems, ...technicianItems, { divider: true }, footerItem];
    }

    if (normalizedRole === 'admin') {
      return [...commonItems, { divider: true }, footerItem];
    }
    

    if (normalizedRole === 'company_admin') {
      return [...commonItems, ...fullAdminItems, { divider: true }, footerItem];
    }

    if (['company_owner', 'private_provider'].includes(normalizedRole)) {
      return [...commonItems, formBuilderItem, { divider: true }, footerItem];
    }

    if (normalizedRole === 'customer') {
      return [...commonItems, footerItem];
    }

    return [...commonItems, footerItem];
  };

  const menuItems: MenuItem[] = getMenuItems();

  return (
    <div className="fixed top-0 right-0 z-[100] w-80 max-w-full h-[100dvh] bg-white shadow-2xl flex flex-col transition-transform duration-300">
      {/* Header */}
      <div className="p-4 border-b flex flex-col items-center relative">
        <button onClick={onClose} className="absolute top-4 right-4 text-xl">
          <FaTimes />
        </button>

        <img
          src={user?.photoURL || "/default-avatar.png"}
          alt="Avatar"
          className="w-16 h-16 rounded-full"
        />
        <p className="mt-2 text-gray-600 text-sm">Welcome</p>
        <p className="font-semibold text-gray-800 mt-1 text-center">
          {user?.displayName || "User"}
        </p>
      </div>

      {/* Menu */}
      <nav className="flex-1 overflow-y-auto p-4 space-y-4 scrollbar-thin scrollbar-thumb-gray-300 scrollbar-track-transparent">
        {menuItems.map((item, index) =>
          'divider' in item ? (
            <hr key={index} className="my-2 border-gray-200" />
          ) : (
            <button
              key={index}
              onClick={() => {
                if (item.path) router.push(item.path);
                item.onClick?.();
                onClose();
              }}
              className="flex items-center space-x-3 w-full text-left text-gray-700 hover:text-black"
            >
            {'icon' in item && (
              typeof item.icon === 'function' ? (
                <item.icon />
              ) : (
                <FontAwesomeIcon icon={item.icon} className="w-4 h-4" />
              )
            )}
              <span>{item.label}</span>
            </button>
          )
        )}
      </nav>

      {/* Footer */}
      <div className="p-4 border-t">
        <button
          onClick={() => {
            signOutUser();
            onClose();
          }}
          className="w-full border border-gray-300 rounded-lg py-2 text-black hover:bg-[#00d289] transition"
        >
          Sign out
        </button>
      </div>
    </div>
  );
};

export default UserSidebar;
