'use client';

import { useState, useMemo } from "react";
import { collection, addDoc, serverTimestamp } from "firebase/firestore";
import { db } from "@/src/firebaseConfig";
import { Input } from "@/src/components/ui/input";
import { Textarea } from "@/src/components/ui/textarea";
import { Button } from "@/src/components/ui/button";
import { cn } from "@/src/lib/utils";
import { useEbikeData } from "@/src/hooks/useEbikeData";
import { useUser } from "@/src/context/AuthContext";

interface ReportIssueFormProps {
  companyId: string;
  companyName: string;
  stationId: string;
  stationName: string;
}

const issueOptions = [
  { value: "Flat Tire", label: "üö≤ Flat Tire" },
  { value: "Battery Problem", label: "üîã Battery Problem" },
  { value: "Brake Issue", label: "üõë Brake Issue" },
  { value: "Motor Problem", label: "‚ö° Motor Problem" },
  { value: "Other", label: "‚ùì Other" },
];

export default function ReportIssueForm({
  companyId,
  companyName,
  stationId,
  stationName,
}: ReportIssueFormProps) {
  const { user } = useUser();
  const { ebikes } = useEbikeData({ companyId });

  const [selectedEbike, setSelectedEbike] = useState<{ id: string; vin: string; plateNumber?: string } | null>(null);
  const [searchText, setSearchText] = useState("");
  const [ebikeDropdownOpen, setEbikeDropdownOpen] = useState(false);

  const [issueType, setIssueType] = useState("");
  const [issueDropdownOpen, setIssueDropdownOpen] = useState(false);

  const [description, setDescription] = useState("");
  const [submitting, setSubmitting] = useState(false);
  const [success, setSuccess] = useState(false);

  const filteredEbikes = useMemo(() => {
    return ebikes.filter((bike) =>
      (bike.vehicleID || "").toLowerCase().includes(searchText.toLowerCase())
    );
  }, [searchText, ebikes]);

  const handleSubmit = async () => {
    if (!selectedEbike || !issueType || !companyId) {
      alert("Please fill all required fields.");
      return;
    }

    setSubmitting(true);

    await addDoc(collection(db, "vehicleIssues"), {
      companyId,
      companyName,
      stationId,
      stationName,
      ebikeId: selectedEbike.id, // Firestore ID
      vin: selectedEbike.vin,    // VIN
      plateNumber: selectedEbike.plateNumber || '', // Plate number
      issueType,
      description,
      photos: [],
      status: "pending",
      reportedBy: user?.displayName || null,
      reportedAt: serverTimestamp(),
      updatedAt: serverTimestamp(),
    });

    setSubmitting(false);
    setSuccess(true);
    setSelectedEbike(null);
    setIssueType("");
    setDescription("");

    setTimeout(() => setSuccess(false), 3000);
  };

  return (
    <div className="space-y-6">

      {/* Ebike VIN Select */}
      <div className="relative space-y-2">
        <button
          onClick={() => setEbikeDropdownOpen(!ebikeDropdownOpen)}
          className="w-full h-12 text-lg border rounded-lg px-4 flex items-center justify-between"
        >
          {selectedEbike ? `üö≤ VIN: ${selectedEbike.vin} (Plate: ${selectedEbike.plateNumber || '-'})` : "Select Ebike (VIN)"}
          <span>‚ñº</span>
        </button>
        {ebikeDropdownOpen && (
          <div className="absolute z-10 bg-white border rounded-lg shadow mt-2 w-full max-h-72 overflow-y-auto space-y-1 p-2">
            <Input
              placeholder="Search Ebike..."
              className="w-full h-10"
              value={searchText}
              onChange={(e) => setSearchText(e.target.value)}
            />
            {filteredEbikes.length === 0 && <div className="p-2 text-gray-500">No Ebikes found</div>}
            {filteredEbikes.map((ebike) => (
              <div
                key={ebike.id}
                className={cn("px-4 py-2 hover:bg-gray-100 cursor-pointer", selectedEbike?.id === ebike.id && "bg-gray-100 font-semibold")}
                onClick={() => {
                  setSelectedEbike({
                    id: ebike.id,
                    vin: ebike.vehicleID,
                    plateNumber: ebike.plateNumber,
                  });
                  setEbikeDropdownOpen(false);
                }}
              >
                üö≤ VIN: {ebike.vehicleID} <span className="text-xs text-gray-500 ml-2">Plate: {ebike.plateNumber || "-"}</span>
              </div>
            ))}
          </div>
        )}
      </div>

      {/* Issue Type Select */}
      <div className="relative">
        <button
          onClick={() => setIssueDropdownOpen(!issueDropdownOpen)}
          className="w-full h-12 text-lg border rounded-lg px-4 flex items-center justify-between"
        >
          {issueType || "Select Issue Type"}
          <span>‚ñº</span>
        </button>
        {issueDropdownOpen && (
          <div className="absolute z-10 bg-white border rounded-lg shadow mt-2 w-full">
            {issueOptions.map((opt) => (
              <div
                key={opt.value}
                className={cn("px-4 py-2 hover:bg-gray-100 cursor-pointer", issueType === opt.value && "bg-gray-100 font-semibold")}
                onClick={() => {
                  setIssueType(opt.value);
                  setIssueDropdownOpen(false);
                }}
              >
                {opt.label}
              </div>
            ))}
          </div>
        )}
      </div>

      <Textarea
        placeholder="Description (optional)"
        className="w-full text-lg"
        value={description}
        onChange={(e) => setDescription(e.target.value)}
      />

      <Button
        className="w-full h-12 text-lg"
        onClick={handleSubmit}
        disabled={submitting}
      >
        {submitting ? "Submitting..." : "Submit Issue"}
      </Button>

      {success && (
        <div className="bg-green-100 border border-green-300 text-green-800 p-4 rounded text-center font-medium">
          ‚úÖ Issue reported successfully!
        </div>
      )}
    </div>
  );
}
